.n-dl-wizard {

  .modal-body {
    padding: 0;
    border-top: none;
  }

  .navbar {
    min-height: 40px;

    .navbar-inner > ul {
      li {
        > a {
          height: 40px;
          width: 40px;
          border-radius: 40px;
          background-image: radial-gradient(circle, #f1f1f1, #E0E0E0, #A9A9A9);
          padding: 0;

          > span {
            display: inline-block;
            height: 28px;
            width: 28px;
            border-radius: 28px;
            font-weight: bold;
            font-size: 16px;
            background-color: #f7f7f7;
            margin: 6px 0 0 6px;
            line-height: 28px;
            color: #000;
            padding: 0;
            text-align: center;
            border: 1px solid #c7c9c9;
          }
        }

        &:not(:last-child)::after {
          content: "";
          position: absolute;
          left: 40px;
          right: -2px;
          top: 16px;
          border-top: 1px solid #c7c9c9;
          border-bottom: 1px solid #c7c9c9;
          #gradient.vertical(#C1C1C1, #f1f1f1);
          height: 7px;
        }

        &.active, &.passed {
          > a {
            background-image: linear-gradient(to bottom, #0a75b2, #69b2cb);
          }
        }

        &.passed::after {
          background-image: linear-gradient(to bottom, #0a75b2, #69b2cb);
          transition: background-image 0.2s ease-in-out;
        }
      }
    }
  }

  .tab-content {
    border: none;
    padding: 0;
    box-shadow: none;
    background: none;

    .n-dl-wizard-step-description {
      padding-bottom: 18px;
      border-bottom: 1px solid #d8d8d8;
    }

    .n-dl-wizard-step-content {
      padding-top: 18px;
    }
  }
}