@font-face {
  font-family: 'Icons';
}

// Catchall baseclass
.icon {
  position: relative;
  display: inline-block;
  font-family: 'Icons';
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// Alert icons
.icon-ok              { &:before { content: "\e600"; color: @brand-success;} }
.icon-ok-disabled       { &:before {content: "\e600"; color: @gray-light;} }
.icon-error             { &:before { content: "\e601"; color: @brand-danger;} }
.icon-info              { &:before { content: "\e602";  color: @brand-info;} }
.icon-warning           { &:before { content: "\e603";  color: @brand-warning;} }
.icon-edited            { &:before { content: "\e62f";  color: @blue-selection;} }
.icon-edited-disabled   { &:before {content: "\e62f"; color: @gray-light;} }



//fault icons
.icon-fault-critical     { &:before { content: "\e629";  color: @fault-critical;} }
.icon-fault-major     { &:before { content: "\e634";  color: @fault-major;} }
.icon-fault-minor     { &:before { content: "\e610";  color: @fault-minor;} }
.icon-fault-warning     { &:before { content: "\e61b";  color: @fault-warning;} }
.icon-fault-cleared     { &:before { content: "\e624";  color: @fault-cleared;} }
.icon-fault-unknown     { &:before { content: "\e61a";  color: @fault-unknown;} }

//Tools icons
.icon-edit-tool     { &:before { content: "\e62e";  color: @gray-global;} }
.icon-expand     { &:before { content: "\e631";  color: @gray-global;} }
.icon-close              { &:before { content: "\e604";  color: @gray-global;} }
.icon-close-rounded     { &:before { content: "\e626";  color: @gray-global;} }
.icon-collapse     { &:before { content: "\e611";  color: @gray-global;} }
.icon-collapse-rounded     { &:before { content: "\e627";  color: @gray-global;} }
.icon-cancel     { &:before { content: "\e623";  color: @gray-global;} }
.icon-help     { &:before { content: "\e608";  color: @gray-global;} }
.icon-calendar-menu     { &:before { content: "\e636";  color: @gray-global;} }
.icon-arrow     { &:before { content: "\e61d";  color: @gray-global;} }
.icon-refresh     { &:before { content: "\e615";  color: @gray-global;} }
.icon-filter     { &:before { content: "\e605";  color: @gray-global;} }
.icon-search     { &:before { content: "\e617";  color: @gray-global;} }

//Navigation icons
