.icon-goto     { &:before { content: "\e607";  color: @gray-global;} }
.icon-dashboard     { &:before { content: "\e62a";  color: @gray-global;} }
.icon-dropdown-menu     { &:before { content: "\e613";  color: @gray-global;} }
.icon-back     { &:before { content: "\e61e";  color: @gray-global;} }
.icon-first     { &:before { content: "\e606";  color: @gray-global;} }
