//Base64 encoded spinner
.icon-spinner {
    &:before {
        content: "";
        display: inline-block;
        background-image: url('data:image/gif;base64,');
        height: 16px;
        width: 16px;
        background-size: 16px 16px;
    }

    &-large {
        &:before {
            height: 40px;
            width: 40px;
            background-size: 40px;
        }
    }

}
