@import "../bootstrap/less/progress-bars.less";
@import "variables.less";

// OT3 p 157 + p 28

.progress {
    background-color: @progress-bg; // empty area
    box-shadow: inset 1px 1px 2px 0 @progress-drop-shadow-color;
}

.progress-bar {
    #gradient > .vertical(@progress-gradient-start, @progress-gradient-end);
    box-shadow: inset 1px 1px 2px 0 lighten(black, 20%);
}

// Nokia style: percentage value outside the bar
.progress-wrapper {
    overflow: hidden;
    display: inline-block;
    > .progress {
        float: left;
        width: 80%;
        white-space: nowrap; // Force items to display on the same line
    }
}

.progress-value-inverse {
    color: @progress-text-color;
    padding-left: 10px;
}

.n-progress-verification {
    font-size: @progress-verification;
}

div.progress-wrapper > span.progress-value:after {  // Last span element's pseudo element
    content: "";
    display: block;
}

.progress-small:extend(.progress all) {
    height: 10px;
}
