@import "../bootstrap/less/panels.less";

@panel-border-radius: 7px;

.panel-basic-gradient() {
    #gradient > .vertical(@panel-basic-gradient-start, @panel-basic-gradient-end);
}

.panel {
    border: none;
    .panel-basic-gradient();
    .border-top-radius(@panel-border-radius);

    &-blue-cap {
        border-top: 3px solid @blue-hover;
    }

    &-simple-shadow {
        .box-shadow(0 2px 5px @panel-shadow-color);
    }

    &-shadow {
        position: relative;
        &:after, &:before {
            z-index: -1;
            position: absolute;
            content: "";
            bottom: 15px;
            left: 10px;
            width: 100px;
            top: 80%;
            .box-shadow(0 15px 10px @panel-shadow-color);
            .rotate(-3deg);
        }
        &:after {
            left: auto;
            right: 10px;
            .rotate(3deg);
        }
    }

    &-body {
        padding: 0;
        .panel-basic-gradient();
        .border-bottom-radius(@panel-border-radius);
    }

    h1 {
        margin-top: 0;
        margin-bottom: 0;
    }
    h2 {
        margin-top: 5px;
        margin-bottom: 5px;
    }

    &-single-row {
        height: 32px;
        padding: 0px 22px 0px 22px;
    }

    &-section {
        padding: 12px 22px 22px 22px;
    }

    &-single-row, &-section {
        border-top: 1px solid @panel-section-light-border;
        border-bottom: 1px solid @panel-section-dark-border;
        &:last-child {
            border-bottom: none;
        }
    }

    &-heading {
        height: 46px;
        padding-left: 14px;
        padding-right: 14px;
        padding-bottom: 15px;
        border-bottom: 1px solid @panel-section-dark-border;

        #gradient > .vertical(@panel-heading-gradient-start, @panel-heading-gradient-end);

        &-transparent {
            background: none;
            filter: e(%("progid:DXImageTransform.Microsoft.gradient(enabled = false)"));
            border-bottom: none;

            & + .panel-body > .panel-section:first-child {
                border: none;
            }
        }

        .n-label-header {
            margin: 0;
        }
    }
}
