@import "../bootstrap/less/buttons.less";

@button-text-shadow-white: 0px 1px 0px #fff;
@button-text-shadow-dark: 0px -1px 0px lighten(#000, 10%);
@button-default-border: 1px solid @input-border;
@button-disabled-border: 1px solid @input-border-disabled;
@button-drop-shadow: 0px 1px 3px 0px @button-drop-shadow-color;
@button-drop-shadow-disabled: 0px 1px 3px 0px @input-border-disabled;

.btn {
    margin: 4px;
    #gradient > .vertical(@button-basic-gradient-start, @button-basic-gradient-end);
    color: @gray-dark;
    text-shadow: @button-text-shadow-white;
    border: 1px solid @input-border;
    box-shadow:0px 1px 3px 0px @button-drop-shadow-color, inset 0px 1px 0px 0px lighten(#fff, 30%);
    outline: none !important;
    border-radius: 7px;

    &:hover {
        border-color: @blue-hover;
        box-shadow: 0px 1px 3px 0px @button-drop-shadow-color, inset 0px 0px 0px 1px lighten(@blue-hover, 30%);
    }

    &:focus {
        #gradient > .vertical(@button-focus-gradient-start, @button-focus-gradient-end);
        box-shadow: @button-drop-shadow;
        border-color: @button-active-focus-border-color;
        color: #fff;
        text-shadow: @button-text-shadow-dark;
        outline: none;
    }

    &:active {
        #gradient > .vertical(@button-active-gradient-start, @button-active-gradient-end);
        box-shadow: none;
        color: #fff;
        text-shadow: @button-text-shadow-dark;
        border-color: @button-active-focus-border-color;
    }

    &[disabled] {
        color: @input-color-disabled;
        #gradient > .vertical(@button-disabled-gradient-start, @button-disabled-gradient-end);
        border: @button-disabled-border;
        box-shadow: @button-drop-shadow-disabled;
        text-shadow: none;
        &:hover {
            border: @button-disabled-border;
            box-shadow: @button-drop-shadow-disabled;
        }
    }

    &-icon,
    &-icon-button{
        height: 32px;
        padding: 6px 8px 8px 8px;

        > span:first-child.icon + span {
            padding-left: 5px;
            margin-right: 12px;
        }

        &:hover {
            #gradient > .vertical(@button-basic-gradient-start, @button-basic-gradient-end);
            color: @gray-dark;
            border:1px solid @blue-hover;
            box-shadow: 0px 1px 3px 0px @button-drop-shadow-color, inset 0px 0px 0px 1px lighten(@blue-hover, 30%);
        }

        &:active, &:focus {
            > .icon:before {
                color: #fff;
            }
        }

        &:focus {
            #gradient > .vertical(@button-focus-gradient-start, @button-focus-gradient-end);
            box-shadow: @button-drop-shadow;
            border-color: @button-active-focus-border-color;
            color: #fff;
            text-shadow: @button-text-shadow-dark;
            outline: none;
        }

        &:active {
            #gradient > .vertical(@button-active-gradient-start, @button-active-gradient-end);
            box-shadow: none;
            color: #fff;
            text-shadow: @button-text-shadow-dark;
            border-color: @button-active-focus-border-color;
        }
    }

    &-icon {
        background: none;
        border: 1px solid transparent;
        box-shadow: none;

        &[disabled] {
            background: none;
            border: none;
            box-shadow: none;

            &:hover, &:active, &:focus {
                border: none;
                box-shadow: none;
                text-shadow: none;
            }

            > .icon:before {
                color: @gray-light;
            }
        }
    }

    &-large {
        font-size: 18px;
        height: 42px;
        min-width: 104px;
        padding-left: 24px;
        padding-right: 24px;
        border-radius: 7px;
    }

    &-standard {
        font-size: 14px;
        height: 32px;
        min-width: 82px;
        padding-top: 6px;
        padding-left:19px;
        padding-right:19px;
        border-radius: 7px;
    }

    &-small {
        font-size: 14px;
        height: 26px;
        min-width: 70px;
        padding-left: 13px;
        padding-right: 13px;
        padding-top: 2px;
        border-radius: 5px;
    }

    &-close, &-minimize {
        height: 21px;
        width: 21px;
        border-radius: 10px;
        font-weight: bold;
        padding: 0px;
    }

    &-close-dlg {
        height: 18px;
        width: 18px;
        border-radius: 50%;
        padding: 0;
        box-sizing: content-box !important;
    }

    &-close:after, &-close-dlg:after {
        font-family: 'Icons';
        content: "\e604";       // Icon size is 15x14
        font-weight: normal;
        font-style: normal;
        text-decoration: none;
        border-bottom: none;
        line-height: 1em;
    }

    &-minimize:after {
        content: "-";
    }

    &-action {
        #gradient > .vertical(@button-focus-gradient-start, @button-focus-gradient-end);
        color: #fff;
        text-shadow: @button-text-shadow-dark;
        box-shadow: @button-drop-shadow;
        border: 1px solid @button-active-focus-border-color;
        margin: 4px;

        &:hover {
            #gradient > .vertical(@action-button-hover-gradient-start, @action-button-hover-gradient-end);
            border-color: @input-border-hover;
            color: #fff;
            box-shadow: @button-drop-shadow;
        }

        &:active {
            #gradient > .vertical(@button-active-gradient-start, @button-active-gradient-end);
            color: #fff;
            text-shadow: @button-text-shadow-dark;
            border-color: @button-active-focus-border-color;
            box-shadow: none;
        }

        &[disabled] {
            #gradient > .vertical(@action-button-disabled-gradient-start, @action-button-disabled-gradient-end);
            color: #f3f3f3;
            box-shadow: @button-drop-shadow-disabled;
            border-color: @action-button-disabled-border-color;
            text-shadow: none;
            &:hover {
                box-shadow: @button-drop-shadow-disabled;
                border: 1px solid @action-button-disabled-border-color;
            }
        }
    }

    &-help {
        height: 18px;
        width: 18px;
        border-radius: 9px;
        border: 1px solid @blue-selection;
        padding: 0px;
        color: #fff;
        font-weight: bold;
        background: @blue-selection;
        box-shadow: none;
        text-shadow: none;

        &:hover, &:active, &:focus {
            background: @blue-selection;
            border: 1px solid @blue-hover;
            box-shadow: none;
            text-shadow: none;
        }

        &:disabled {
            border: 1px solid @gray-light;
            background: @gray-light;
        }

        &:after {
            content: "?";
            box-shadow: none;
            text-shadow: none;
            position: relative;
            top: -1px;
        }
    }
}

.btn-group {
    margin: 10px;
    .btn {
        margin: 0;
        float: left;

        &:first-child {
            .border-right-radius(0px);
        }

        &:last-child {
            .border-left-radius(0px);
        }

        &:not(:last-child):not(:first-child) {
            border-radius: 0px;
        }

    }

    //TODO: Replace this with a generic reuseable clearfix.
    &:after {
        content: " ";
        clear: both;
        display: block;
        font-size: 0;
        height: 0;
    }

}
