@import "../bootstrap/less/navbar.less";

// Note impacting components that contain banner specific settings: dropdown.less

@n-banner-1st-row-left-gutter: 0px;
@n-banner-1st-row-right-gutter: 0px;
@n-banner-2nd-row-left-gutter: 0px;
@n-banner-2nd-row-right-gutter: 0px;

@n-banner-height: 118px;

@n-banner-1st-gradient-white-height: 78px;
@n-banner-1st-gradient-white-height-condensed: 51px;
@n-banner-1st-gradient-white-start: #e5e5ea;
@n-banner-1st-gradient-white-end: #fff;
@n-banner-1st-gradient-blue-height: 61px;
@n-banner-1st-gradient-blue-height-condensed: 45px;
@n-banner-1st-gradient-blue-start: #1852aa;
@n-banner-1st-gradient-blue-end: #0b2a67;
@n-banner-2nd-gradient-blue-height: 40px;
@n-banner-2nd-gradient-blue-start: #1a64c1;
@n-banner-2nd-gradient-blue-end: #0e3277;
