@n-banner-2nd-icon-text-shadow: 0 -1px rgba(0, 0, 0, 1);
@n-banner-min-width: 1072px;


@n-banner-breakpoint: 666px;

.n-banner {
    position: relative;
    min-height: @n-banner-height;
    border-top: 1px solid #fff;

    padding-top: 16px;

    // Prevent floats from breaking the navbar
    &:extend(.clearfix all);


    //@media (max-width: @n-banner-min-width) {
    //    min-width: @n-banner-min-width;
    //}
    width:100%;
    min-width:@n-banner-min-width;
    @media (max-width: (@grid-float-breakpoint - 2px)) {
        width: 100%;
        min-width:100%;
    }
    @media (min-width: @grid-float-breakpoint) {
        border-radius: @navbar-border-radius;
    }

}

.n-banner-header {
    &:extend(.clearfix all);

    @media (min-width: @grid-float-breakpoint) {
        float: left;
    }
}

.n-banner-collapse {
    overflow-x: visible;
    padding-right: @navbar-padding-horizontal;
    padding-left: @navbar-padding-horizontal;
    border-top: 1px solid transparent;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, .1);
    &:extend(.clearfix all);
    -webkit-overflow-scrolling: touch;

    &.in {
        overflow-y: auto;
    }

    @media (min-width: @grid-float-breakpoint) {
        width: auto;
        border-top: 0;
        box-shadow: none;

        &.collapse {
            display: block !important;
            visibility: visible !important;
            height: auto !important;
            padding-bottom: 0; // Override default setting
            overflow: visible !important;//
        }

        &.in {
            overflow-y: visible;
        }

    }
}

.container,
.container-fluid {
    > .n-banner-header,
    > .n-banner-collapse {
        margin-right: -@navbar-padding-horizontal;
        margin-left: -@navbar-padding-horizontal;

        @media (min-width: @grid-float-breakpoint) {
            margin-right: 0;
            margin-left: 0;
        }
    }
}

