// "Wave" transitions

.n-banner-1st-blue-to-gray {
    height: 61px;
    min-width: 100px;
    padding: 0;
    position: relative;
    width: 100px;

    > .blue-mask {
        background: linear-gradient(to bottom, #1852aa -30px, #0b2a67);
        bottom: 0;
        height: 50%;
        left: 0;
        position: absolute;
        width: 100%;
    }
    > .gray-mask {
        background: linear-gradient(to bottom, #d6d7d8, #9ca1a4 61px);
        height: 50%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
    > .blue-corner {
        background: linear-gradient(to bottom, #1852aa, #0b2a67);
        border-radius: 0 100px 0 0;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: +1;
        //glitch fix in ie
        left: -1px;
        width: calc(50% + 2px);

    }
    > .gray-corner {
        background: linear-gradient(to bottom, #d6d7d8, #9ca1a4);
        border-radius: 0 0 0 100px;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: +1;
        //glitch fix in ie
        width: 63px;
        right: -1px;
    }
}

.n-banner-1st-gray-to-white {
