.n-banner-overflow-control {
    width: 0;
    .n-banner-dropdown-toggle {
        display: block;
        height: @n-banner-2nd-gradient-gray-height;
        box-shadow: 0 0 8px 0 #000;
        border-left: 1px solid;
        .icon-dropdown-menu{
            &:before{
                color: @dropdown-menu-color;
            }
            left: 13px;
            top: 10px;
            z-index: +100;
        }
        .overflow-toggle-area{
            &:before{
                content: '';
                height: @n-banner-2nd-gradient-gray-height;
                width: 57px;
                position: absolute;
                top: 0;
                left: 0;
                background: linear-gradient(to bottom, #d6d7d8, #9ca1a4);
                border-radius: 0 100px 0 0;
                z-index: 99;
            }
            &:after{
                content: '';
                height: 21px;
                width: 83px;
                position: absolute;
                left: 0;
                bottom: 0;
                background: linear-gradient(to bottom, #b9bcbe, #9ca1a4);
                z-index: 99;
            }
        }
        .overflow-toggle-area-cover{
            &:after{
                content: '';
                width: 55px;
                height: @n-banner-2nd-gradient-blue-height;
                position: absolute;
                top: 0;
                left: 46px;
                border-radius: 0 0 0 100px;
                #gradient > .vertical(@n-banner-2nd-gradient-gray-start, @n-banner-2nd-gradient-gray-end);
                z-index: 100;
            }
            display: none;
        }
        &:hover[aria-expanded=false] {
            .overflow-toggle-area{
                &:before {
                    border-left: 2px solid @blue-hover;
                    border-top: 1px solid @blue-hover;
                }
                &:after{
                    border-left: 2px solid @blue-hover;
                    border-bottom: 2px solid @blue-hover;
                }
            }
        }
        &:focus,&:hover,
        &[aria-expanded=true]{
            .overflow-toggle-area{
                &:before {
                    background: @dropdown-menu-background;
                }
                &:after{
                    background: @dropdown-menu-background;
                }
            }
        }
        &[aria-expanded=true]>.overflow-toggle-area{
            &:before {
                border-left: 1px solid rgba(0, 0, 0, 0.15)
            }
            &:after{
                left: 1px;
            }
        }
    }
    .dropdown-menu {
        border-top: none;
        > li > a{
            &:hover,&:focus{
                background-color: white;
                >span {
                    border-bottom-color: @blue-selection;
                }
            }
            >span{
                padding-bottom: 1px;
                border-bottom: 1px solid transparent;
            }
        }
    }
}

@n-banner-tab-text-color: #333;
@n-banner-tab-background-active: #d8dae0;
@n-banner-tab-background-hover: #ced0d8;
@n-banner-tab-border-color-hover: #c3c3c3;
.n-banner-corner-on-tab(@border-color, @background-color){
    outline: 0;
    & > span {
        color: black;
        text-shadow: none;
        margin-bottom: -1px;
        &:before,
        &:after {
            content: '';
            position: absolute;
            width: @nav-tabs-border-radius;
            height: @nav-tabs-border-radius;
            bottom: 0;
            border-bottom: 1px solid @border-color;
            #gradient > .vertical(@n-banner-2nd-gradient-gray-start, @n-banner-2nd-gradient-gray-end, -85%);
            z-index: 3;
        }
        &:before {
            margin-left: -@nav-tabs-border-radius;
            border-right: 1px solid @border-color;
            left: 0;
            border-radius: 0 0 @nav-tabs-border-radius 0;
        }
        &:after {
            margin-right: -@nav-tabs-border-radius;
            border-left: 1px solid @border-color;
            right: 0;
            border-radius: 0 0 0 @nav-tabs-border-radius;
        }
    }
    &:after {
        content: '';
        position: absolute;
        width: calc(100% + 14px);
        height: @nav-tabs-border-radius;
        background: @background-color;
        bottom: 0;
        left: -7px;
        z-index: 2;
    }
}
.n-banner-corner-on-drop-down-tab(@border-color, @background-color){
    .n-banner-corner-on-tab(@border-color, @background-color);
    &:after {
        left: 0px;
        width: calc(100% + 7px);
    }
    & > span:before {
        visibility: hidden;
    }
}

.n-banner-tabs {
    position: relative;
    margin-left: -15px;
    padding-left: 15px;
    #gradient > .vertical(@n-banner-2nd-gradient-gray-start, @n-banner-2nd-gradient-gray-end);
    height: @n-banner-2nd-gradient-blue-height;
    padding-top: 0;
    > li {
        height: 100%;
        & > a {
            height: 100%;
            line-height: 40px;
            color: @n-banner-tab-text-color;
            font-size: 14px;
            font-weight: normal;
            line-height: calc(@n-banner-2nd-gradient-blue-height - 1px);
            padding: 0 15px;
            text-shadow: 0 1px rgba(255, 255, 255, 0.65);
            border: 1px solid transparent;
            border-bottom: none;
            border-radius: 7px 7px 0 0;
            &:focus:not(.disabled) {
                background: @n-banner-tab-background-active;
                color: black !important;
                text-shadow: none !important;
                border: 1px solid;
                border-bottom: none;
                border-color: @nav-tabs-border-color;
                > span.caret {
                    color: black !important;
                }
                .n-banner-corner-on-tab(@nav-tabs-border-color, @n-banner-tab-background-active);
            }
        }

        &:hover {
            background: @n-banner-tab-background-hover;
            > a {
                background: transparent;
                border-radius: @nav-tabs-border-radius @nav-tabs-border-radius 0 0;
                border: 1px solid;
                border-bottom: none;
                border-color: @n-banner-tab-border-color-hover;
                .n-banner-corner-on-tab(@n-banner-tab-border-color-hover, @n-banner-tab-background-hover);
                > span{
                    border-bottom: 1px solid @blue-hover;
                }
            }

        }
    }

    > li.dropdown {
        .dropdown-menu {
            border-radius: 0 5px 5px 5px;
            margin-top: -1px;
            border-color: @input-border;
        }
        &.open > a {
            background: @dropdown-menu-background;
            box-shadow: 0 1px 3px 0 #a7a7a7;
            border: 1px solid;
            border-bottom: none;
            border-color: @nav-tabs-border-color;
            .n-banner-corner-on-drop-down-tab(@nav-tabs-border-color, @dropdown-menu-background);
            &:after {
                height: 9px;
                margin-bottom: -2px;
                z-index: 1001;
            }
            > span:after {
                z-index: 1002;
            }
            > span:not(:hover){
                border-bottom: none;
            }
        }
    }
    // right most tab will highlight part of the curve when focus / hover
    > li.rightmost-tab {
        margin-right: 0;
        > a  {
            border-radius: 7px 0 0 0;
            padding-right : 0;
            &:focus {
                &:before{
                    position: absolute;
                    content: '';
                    bottom: 0;
                    right: 0;
                    z-index: 3;
                    margin-right: -100px;
                    background: @n-banner-tab-background-active;
                    width: 100px;
                    height: 20px;
                }
                > span:after {
                    height: 39px;
                    width: 50px;
                    background: @n-banner-tab-background-active;
                    margin-right: -50px;
                    border: none;
                    border-radius: 0 100px 0 0;
                }
            }
        }
    }
    > li.rightmost-tab:hover > a:extend(.n-banner-tabs > li.rightmost-tab > a:focus all) {
            &:not(:focus):before,
        &:not(:focus)>span:after {
            background: @n-banner-tab-background-hover;
        }
    }
}
.n-banner-dropdown-toggle {
    border: none;
    box-shadow: none;
    span.caret {
        border-bottom: none !important;
    }
    // +++ Keyboard focusing or mouse opened - keep this location before hovering
    &:focus:not(.disabled):not([aria-expanded=true]) {
        background: @n-banner-tab-background-active !important;
    }
    // +++ Hovering but list not open
    &:hover[aria-expanded=false], &:hover:not([aria-expanded]) {
        color: black;
        border: 1px solid;
        border-bottom: none;
        border-color: @n-banner-tab-border-color-hover;
        box-shadow: none !important;
        background: transparent;
    }
    // +++ List opened
    &[aria-expanded=true] {
        color: black;
        border-radius: 7px 7px 0 0;
        background: @dropdown-menu-background !important;
    }
    &:active:not([aria-expanded]), &:active[aria-expanded=false] {
        #gradient > .vertical(@dropdown-banner-background-hover, @dropdown-banner-background-hover);
        &:hover {
            border-color: @nav-tabs-border-color !important;
            box-shadow: none !important;
        }
    }
}

// White links in banner area
.n-banner-links {

    #gradient > .vertical(@n-banner-2nd-gradient-blue-start, @n-banner-2nd-gradient-blue-end);
    height: @n-banner-2nd-gradient-blue-height;
    padding-top: 0;

    > li > a {
        font-weight: normal;
        color: white !important;
        background-color: transparent;
        text-shadow: 0 1px rgba(0, 0, 0, 0.75);
        &:hover {
            background: transparent;
            border: none;
            outline: 0;
            color: white;
            //background-color: transparent;
            background-clip: text;
            text-shadow: 1px -1px 5px rgba(255, 255, 255, 0.50);

            // TODO make the glow effect around text

            //text-shadow: 0 1px rgba(255, 255, 255, 0.30);
            > span {
                border: none;
            }
        }
        &:focus {
            background-color: transparent !important;
            border: none !important;
        }
        // TODO make the icon with following color setting
        // color: #a8bbc0;
    }
}

