.n-banner.condensed{
    padding-top: 6px;
    min-height: 85px;
    .n-banner-1st-filler-white {
        height: @n-banner-1st-gradient-white-height-condensed;
    }
    .n-banner-1st-filler-blue {
        height: @n-banner-1st-gradient-blue-height-condensed;
        top: 6px;
    }
    .n-banner-2nd-filler-blue,
    .n-banner-2nd-filler-gray {
        top: @n-banner-1st-gradient-white-height-condensed;
    }
    .n-banner-brand,
    .n-banner-1st .n-banner-nav,
    .n-banner-1st-blue-to-gray,
    .n-banner-1st-gray-to-white,
    .n-banner-logo-img {
        height: @n-banner-1st-gradient-blue-height-condensed;
    }

    .n-banner-1st-gray-to-white > .white-corner {
        background: linear-gradient(to bottom, #e5e5ea -6px, #ffffff);
    }
    .n-banner-1st .n-banner-nav {
        padding-top: 0;
    }
    .n-banner-logo-img {
        padding-top: 11px;
    }
    .n-banner-brand {
        line-height: @n-banner-1st-gradient-blue-height-condensed;
    }
}

.n-banner-1st {
    li > a {
        padding-top: 15px;
        padding-bottom: 15px;
    }
    .n-banner-dropdown-toggle[aria-expanded=false] {
        &:hover {
            border: none;
        }

        &:focus{
            background-color: transparent !important;
        }
    }
    .n-banner-dropdown-toggle[aria-expanded=true] {
        & > span {
            &:before {
                content: '';
                height: 7px;
                width: 7px;
                background-color: @dropdown-menu-background;
                position: absolute;
                bottom: 0;
                right: 0;
                margin-right: -7px;
                z-index: 1;
            }

            &:after {
                content: '';
                height: 7px;
                width: 7px;
                background-color: #9ea3a6;
                position: absolute;
                bottom: 0;
                right: 0;
                margin-right: -7px;
                border-radius: 0 0 0 7px;
                z-index: 2;
            }
        }
    }

    .dropdown-menu {
        border-radius: 0px 7px 7px 7px;
        border: none;
        > li > a {
            height: 28px;
            line-height: 28px;
            padding-top: 0;
            padding-bottom: 0;
        }
    }
}
