.n-drillDown-content{
  .icon-close-rounded{
    position: absolute;
    z-index: 3;
    top: 0;
    right: 30px;
    margin-top: 30px;
    cursor: pointer;
  }
  border: 1px solid rgba(255,255,255,.65);
  height:500px;
  width: 100%;
  position: relative;
}

.n-drillDown-arrowContainer{
  height: 30px;
  position: relative;
}

.n-drillDown-collapsed{
  display: none;
  position: relative;
}

.n-drillDown-arrow:before {
  content:'';
  position: absolute;
  z-index: 8;
  width: 28px;
  height: 5px;
  margin-left: -14px;
  top:21px;
  background: linear-gradient(to bottom, #626262, #6D6D6D);
}

.n-drillDown-arrow{
  border-style:solid;
  border-color: transparent;
  width:0;
  height:0;
  position: absolute;
  border-width:18px;
  border-bottom-width: 22px;
  border-bottom-color: rgba(255,255,255,.65);
  border-top-width:0;
  z-index: 6;
  margin-left: -21px;
  top:9px;
}

.n-drillDown-arrow:after{
  content:'';
  border-style:solid;
  border-color: transparent;
  width:0;
  height:0;
  position: absolute;
  border-width:18px;
  border-bottom-width: 22px;
  border-bottom-color: #626262;
  border-top-width:0;
  top:1px;
  margin-left:-18px;
  z-index: 7;
}

.n-drillDown-border{
  border-radius:5px;
  box-shadow: 0px 0px 0px 3px #00A1CC inset;
}

.n-drillDown-inner:before{
  content:'';
  background: -webkit-linear-gradient(rgba(0,0,0,.25) , rgba(0,0,0,0));
  background: -o-linear-gradient(rgba(0,0,0,.25), rgba(0,0,0,0));
  background: -moz-linear-gradient(rgba(0,0,0,.25), rgba(0,0,0,0));
  background: linear-gradient(rgba(0,0,0,.25) , rgba(0,0,0,0));
  position: absolute;
  top:0px;
  left:0px;
  height: 50px;
  width:100%;
}

.n-drillDown-inner{
  padding:30px;
  background: #D8D9DA;
  height: 100%;
  width:100%;
  box-shadow: 12px 0 7px -12px rgba(0, 0, 0, .45) inset,-12px 0 7px -12px rgba(0, 0, 0, .45) inset,0 12px 30px -12px rgba(0, 0, 0, .9) inset,0 -12px 30px -12px rgba(0, 0, 0, .9) inset;
}

.n-drillDown-inner:after{
  content:'';
  background: -webkit-linear-gradient(rgba(0,0,0,0) , rgba(0,0,0,.25));
  background: -o-linear-gradient(rgba(0,0,0,0), rgba(0,0,0,.25));
  background: -moz-linear-gradient(rgba(0,0,0,0), rgba(0,0,0,.25));
  background: linear-gradient(rgba(0,0,0,0) , rgba(0,0,0,.25));
  position: absolute;
  bottom:0px;
  left:0px;
  height: 50px;
  width:100%;
}

.n-drillDown-item{
  cursor:pointer;
}




