@import "variables.less";

// OT3 p.129
// Settings work for FuelUX combobox
// NOTE: see also the dropdowns.less + inputfields.less as those styles are used for combobox

.combobox .dropdown-toggle,.n-combobutton .dropdown-toggle {
    border-radius: 0px 7px 7px 0px !important;
    //TODO temporary margin
    margin: 0;
    // +++ Arrow placement
    span.caret {
        margin: 0px 0px 0px 0px;
    }
    &[aria-expanded=true] {
        border-radius: 0px 7px 0px 0px !important;
    }
}

.combobox-open > .form-control.n-inputfield {
    border-bottom-left-radius: 0;
}

.n-combobutton-btn {
    padding-right: 36px;
}

.n-scrollable{
    overflow-y: scroll;
}

// +++ Small variant
.n-combobutton-small {
    .n-combobutton-btn,
    .dropdown-toggle {
        font-size: @font-size-small;
        height: 26px;
        padding-left: 10px !important;
        padding-right: 10px !important;
        padding-top: 2px;
        border-radius: 5px;
        span.caret{
            margin-top: 4px;
        }
        span.selected-label {
            height: 26px;
            line-height: 23px;
            padding: 0 10px 0 0;
        }

        &[aria-expanded=true] {
            border-radius: 6px 6px 0 0;
        }
    }
    .dropdown-menu {
        border-radius: 0 0 6px 6px;
        font-size: @font-size-small;
    }
}

.n-combobutton.open>.btn{
    border-bottom-left-radius: 0;
}
