// Fight FOUT
html.wf-loading:not(.wf-active) body{
  visibility: hidden;
}

@font-face {
    font-family: 'Nokia Pure';
    src: url('../fonts/NokiaPureHWeb_Lt.eot');
    src: url('../fonts/NokiaPureHWeb_Lt.eot?#iefix') format('embedded-opentype'),
         url('../fonts/NokiaPureHWeb_Lt.woff') format('woff'),
         url('../fonts/NokiaPureHeadline_Light.ttf') format('truetype'),
         url('../fonts/NokiaPureHWeb_Lt.svg#NokiaPureHWebLight') format('svg');
    font-weight: 300;
    font-style: normal;
}

