.spinbox {
  display: inline-block;
  position: relative;

  .spinbox-input {
    float: left;
    width: 110px;
    height: 30px;
    padding: 0px 11px;
    font-weight: bold;
    font-size: 14px;
    -webkit-box-shadow: none;
    box-shadow: none;

    &::selection {
      background-color: #0B6DAF;
    }

    &::-ms-clear {
      display: none;
    }
  }

  .spinbox-buttons {
    position: absolute;
    width: 32px;
    top: 0;
    bottom: 0;
    right: 0;
    margin: 0;

    .btn {
      position: relative;
      height: 15px;
      width: 32px;
      margin: 0;
      line-height: 15px;

      &:active, &:focus {
        .icon:before {
          color: #ffffff;
        }
      }
    }

    .spinbox-up {
      border-top-right-radius: 7pt !important;
      border-top-left-radius: 0 !important;
    }

    .spinbox-down {
      border-top-right-radius: 0 !important;
      border-top-left-radius: 0 !important;
      border-bottom-right-radius: 7pt;
      border-bottom-left-radius: 0 !important;
    }

    .icon:focus:before {
      color: red;
    }

  }
}



