@slider-track-height: 8px;
@slider-thumb-size: (@slider-track-height * 2);

.rangeslider {
    background: @slider-track-background;
    display: block;
    width: 100%;
    height: @slider-track-height;
    position: relative;
    box-shadow: inset 2px 2px 1px rgba(0, 0, 0, 0.5), 0px 1px 1px rgba(0, 0, 0, 0.5);
    border-radius: (@slider-track-height / 2);
}

.rangeslider__handle {

    display: inline-block;
    height: @slider-thumb-size;
    width: @slider-thumb-size;
    position: absolute;
    top: (@slider-thumb-size / -4);
    margin: 0px;
    border-radius: (@slider-thumb-size / 2);
    #gradient > .vertical(@button-basic-gradient-start, @button-basic-gradient-end);
    box-shadow:0px 1px 3px 0px @button-drop-shadow-color, inset 0px 1px 0px 0px lighten(#fff, 30%);

    &:before {
        content: " ";
        display: block;
        margin-left: (@slider-thumb-size / 4);
        margin-top: (@slider-thumb-size / 4);
        width: (@slider-thumb-size / 2);
        height: (@slider-thumb-size / 2);
        border-radius: (@slider-thumb-size / 4);
        #gradient > .vertical(@button-focus-gradient-start, @button-focus-gradient-end);
    }

   &:hover {
       border-color: @blue-hover;
       box-shadow: 0px 1px 3px 0px @button-drop-shadow-color, inset 0px 0px 1px 1px @blue-hover;
   }

}

.rangeslider__handle:active, .rangeslider__handle:focus, .rangeslider__handle.active {

    #gradient > .vertical(@button-focus-gradient-start, @button-focus-gradient-end);
    box-shadow: @button-drop-shadow;
    border-color: @button-active-focus-border-color;

    &:before {
      background: #fff;
      box-shadow: inset 0px 1px 0px 0px @button-drop-shadow-color;
    }

}
