@import "../bootstrap/less/dropdowns.less";
@import "variables.less";

// OT3 p.128
// Settings work for FuelUX dropdowns
// NOTE: these settings are impacting directly combobox and banner as well

.btn-group {
    .btn:first-child {
        .border-right-radius(7px);
    }
}

// Button definitions
.dropdown-toggle {
    border-radius: 7px;
    color: @dropdown-text;
    border: 1px solid @input-border;
    box-shadow:0px 1px 3px 0px @button-drop-shadow-color, inset 0px 1px 0px 0px lighten(#fff, 30%);
    #gradient > .vertical(@button-basic-gradient-start, @button-basic-gradient-end);
    .transition(none); //prevents black content in printing together with form-control class
    // Needed for combobox
    outline: none !important;

    // +++ Add border between text area and arrow
    span.selected-label {
        border-right: 1px solid @input-border;
        padding: 0px 11px 0px 0px;
        height: 100%;
    }
    // +++ Arrow placement
    span.caret {
        margin: 0px 0px 0px 8px;
        border-top: 5px solid;
        color: @dropdown-arrow;
    }
    // +++ Hovering but list not open
    &:hover[aria-expanded=false], &:hover:not([aria-expanded]) {
        color: @dropdown-text;
        text-shadow: none;
        border-color: @blue-hover;
        box-shadow: inset 0px 0px 0px 1px lighten(@blue-hover, 30%);
        #gradient > .vertical(@nav-tabs-gradient-top, @nav-tabs-gradient-bottom);
        .selected-label {
            border-color: @blue-hover;
        }
    }
    // List opened
    &[aria-expanded=true] {
        #gradient > .vertical(@nav-tabs-dark-gradient-top, @nav-tabs-dark-gradient-bottom);
        border-radius: 7px 7px 0px 0px !important;
        text-shadow: none;
        span.caret {
            color: @dropdown-arrow;
        }
    }

    // Focus
    &:focus:not(.disabled):not(:hover):not([aria-expanded=true]) {
        #gradient > .vertical(@dropdown-dark-focus-gradient-start, @dropdown-dark-focus-gradient-end);
        color: white;
        span.caret {
            color: @dropdown-arrow-bright;
        }
    }

