@import "variables.less";
.jqx-grid{
  border-radius: 7px;
  box-shadow: 0 1px 3px 0 #888888;
}

.jqx-cell{
  line-height: 26px;
  padding: 0 8px 0 8px;
}

.jqx-grid-cell-alt {  background: @n-table-row-grey;}
.jqx-widget-header{background: linear-gradient(@n-table-th-top,@n-table-th-bottom); font-weight: bolder;}
.no-grid-line {
  .jqx-grid-table .jqx-grid-cell {
    border-width: 0 0 0 1px;
  }
}

.jqx-grid-cell-hover { background: @n-table-row-white-hover; color: #000;}
td[class~="jqx-grid-cell-alt"][class~="jqx-grid-cell-hover"]{background: @n-table-row-grey-hover;}
.jqx-grid-cell-selected,td[class~="jqx-grid-cell-selected"][class~="jqx-grid-cell-hover"] { background: @n-table-row-selected;  border-left: 0px solid transparent; color: #FFFFFF;}

