@import "variables.less";

.fuelux .tree {

    .tree-item {
        margin: 0;

        .tree-item-name {
            display: inline-block;
            width: 100%;
            height:25px;
            text-align: left;
            font-weight: normal;
            padding-left: 5px;
            border: 1px solid transparent;
            border-radius: 0;
            color: @n-tree-text-normal-color;
            &:hover {
                color: @n-tree-text-hover-color;
                .tree-label {
                    border-bottom: 1px solid @n-tree-hover-underline-color;
                }
            }
            &:focus {
                border: 1px solid @n-tree-border-focus-color;
            }

            .fueluxicon-bullet {
                font-size: 10px;
                top: 0;
                margin-right: 5px;
            }

            .glyphicon-ok {
                font-family: fuelux;
                font-size: 10px;
                top: 0;
                &:before {
                    content: '\e009';
                    margin-right: 4px;
                }
            }
        }

        &.tree-selected .tree-item-name {
            background-color: @n-tree-selected-background-color;
            color: @n-tree-text-selected-color;
            &:hover .tree-label {
                border-bottom: 1px solid transparent;
            }
        }

        &:last-child {
            border-bottom: 1px dotted #c3c5d0;
            border-radius:0;
        }
    }

    .icon-folder {
        display: none;
    }

    height: 100%;
    padding: 2px;
    border-radius: 0;
    .tree-branch {
        margin-top: 0;
        margin-bottom: 0;
        .tree-branch-header {
            .glyphicon-play {
                padding-left: 5px;
                padding-right: 0;
                font-size: 14px;
                &:before {
                    content: '\e612';
                    margin-right: 4px;
                }
            }

            .tree-branch-name {
                padding-left: 0;
                width: 100%;
                height: 25px;
                text-align: left;
                border-radius: 0;
                border: 1px solid transparent;
                color: @n-tree-text-normal-color;
                &:hover {
                    color: @n-tree-text-hover-color;
                    .tree-label {
                        border-bottom: 1px solid @n-tree-hover-underline-color;
                    }
                }
                &:focus {
                    border: 1px solid @n-tree-border-focus-color;
                }
            }
        }

        .tree-branch-children {
            margin-left: 0px;
            padding-left: 14px;
        }

        &.tree-selected > .tree-branch-header .tree-branch-name {
            background-color: @n-tree-selected-background-color;
            color: @n-tree-text-selected-color;
            &:hover {
                color: @n-tree-text-selected-color;
            }
        }
    }

    .glyphicon {
        font-family: "Icons";
        top: 3px;
    }

    .tree-open > .tree-branch-header .glyphicon-play {
        top: 0;
    }

}

.fuelux .tree-has-checkbox
{
    .checkbox{
        position:absolute;
        top: 6px;
        display: inline-block;
    }

    .checkbox input[type=checkbox]:indeterminate + label:before {
        content: "\25AC";
        color: #666;
        width: 14px;
        height: 14px;
        left: 0px;
        border-color:#adadad;
        transform: scale(1, 1);
        border-radius: 4px;
        line-height:8px;
        text-align: center;
    }

    .tree-label {
        position: absolute;
        top: 3px;
        margin-left: 18px;
    }

    .icon-item {
        display: inline;
        line-height: 25px;
    }

    .tree-title
    {
        margin-left: 8px;
    }

    .tree-item .tree-item-name .checkbox:hover label:before,
    .tree-branch .tree-branch-name .checkbox:hover label:before{
        border: 1px solid @input-border-hover;
        box-shadow: 0px 1px 3px 0px @input-border, 0px 0px 0px 1px lighten(@input-border-hover, 30%) inset;
    }

    .checkbox input[type="checkbox"]:not(.sr-only):checked ~ label:before,
    .checkbox-inline input[type="checkbox"]:not(.sr-only):checked ~ label:before {
        content: '\e600';
        text-align: center;
        color: #666;
        background: #fff;
        left: 0;
        display: inline-block;
        width: 14px;
        height: 14px;
        border: 1px solid #b2b2b2;
        box-shadow: 0 1px 3px 0 #b2b2b2;
        border-radius: 4px;
        line-height:10px;
        padding: 1px 1px 1px 0;
        font-family: "Icons";
    }
}
