@import "../bootstrap/less/buttons.less";
@import "variables";


@n-toggle-button-active-shadow: inset 0 3px 5px rgba(0, 0, 0, .125);
@n-toggle-button-active-shadow-dark: inset 0 3px 5px rgba(0, 0, 0, .375);

.n-toggle-button {

  &.active {
    border-color: @gray-light;
    color: @gray-global;
    #gradient.vertical(@n-toggle-button-active-bg-grey-top, @n-toggle-button-active-bg-grey-bottom);
    text-shadow: none;
    .icon:before {
      color: @gray-global;
    }
    &:focus {
      border-color: @gray-global;
    }
    &,
    &:hover,
    &:focus {
      box-shadow: @n-toggle-button-active-shadow;
    }
  }

  &-small {
    height: 26px;
    padding-top: 2px;
  }

  &-dark.active {
    color: #fff;
    #gradient > .vertical(@button-active-gradient-start, @button-active-gradient-end);
    .icon:before {
      color: #fff;
    }
  }

  &-color.active {
    color: #fff;
    #gradient.vertical(@n-toggle-button-active-bg-green-top, @n-toggle-button-active-bg-green-bottom);
    .icon:before {
      color: #fff;
    }
    &,
    &:focus,
    &:hover {
      box-shadow: @n-toggle-button-active-shadow-dark;
    }
  }

}