@import "variables.less";
@import "../bootstrap/less/list-group.less";

//== List group
//
//##

// Normal list (light-weight implementation)
ul.list-group {
    cursor: default;
    @list-group-border-radius: 7px; //@input-border-radius;
    text-shadow: 0 1px rgba(255, 255, 255, 0.65);

    //    li:not(:first-child):not(:last-child) {
    //        //border: 1px solid @list-group-border; //transparent;
    //        border-bottom: 1px solid transparent;
    //        border-top: 1px solid transparent;
    //        padding: 3px 10px 3px 10px;
    //        color: red;
    //    }

    // Todo need to try media screen (or print) to normalize the printing of first and last cell borders

    // Border settings for the list items
    > li {
        border: 1px solid transparent;
        border-left: 1px solid @list-group-border;
        border-right: 1px solid @list-group-border;
        padding: 3px 10px 3px 10px;
    }

    > li:first-child {
        border-top: 1px solid @list-group-border;
        padding: 6px 10px 4px 10px;
    }

    > li:last-child {
        border-bottom: 1px solid @list-group-border;
        padding: 4px 10px 6px 10px;
    }
}

@media print {
    ul.list-group {
        cursor: default;
        @list-group-border-radius: 7px; //@input-border-radius;
        text-shadow: 0 1px rgba(255, 255, 255, 0.65);

        //    li:not(:first-child):not(:last-child) {
        //        //border: 1px solid @list-group-border; //transparent;
        //        border-bottom: 1px solid transparent;
        //        border-top: 1px solid transparent;
        //        padding: 3px 10px 3px 10px;
        //        color: red;
        //    }

        // Todo need to try media screen (or print) to normalize the printing of first and last cell borders

        // Border settings for the list items
        > li {
            border: 1px solid transparent;
            border-left: 1px solid @list-group-border;
            border-right: 1px solid @list-group-border;
            padding: 3px 10px 3px 10px;
        }

        > li:first-child {
            border-top: 1px solid @list-group-border !important;
            padding: 6px 10px 4px 10px;
        }

        > li:last-child {
            border-bottom: 1px solid @list-group-border !important;
            padding: 4px 10px 6px 10px;
        }
    }
}

.list-group-item {
    border-bottom: 1px solid;
    font-weight: normal;
    color: @gray-nav; //is this used also if the list cannot be navigated/clicked?
}

// Disabled list
ul.disabled {
    cursor: default;
    color: @list-group-disabled-text-color;
    > li.list-group-item {
        color: @list-group-disabled-text-color;
    }
    &:before {
        color: @list-group-disabled-text-color;
    }
    &:focus {
        outline: none;
    }
}

// Striped list version
.n-list-striped {
    > li:nth-child(odd) {
        background-color: @n-table-row-white;
    }
    > li:nth-child(even) {
        background-color: @n-table-row-grey;
    }
}

// Selectable list
// This is for making selection support into list (without it being select-input component)
.n-list-group:extend(.list-group all) {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.n-list-group-small:extend(.n-list-group all) {
    @list-group-border-radius: 6px; //@input-border-radius;
    > li {
        padding: 1px 10px 1px 10px;
    }
    > li:first-child {
        padding: 4px 10px 2px 10px;
    }
    > li:last-child {
        padding: 2px 10px 4px 10px;
    }
}

.n-list-group-item:extend(.list-group-item all) {
    color: @gray-nav;
    &:hover {
        cursor: pointer;
        color: black;
        span {
            border-bottom: 1px solid @blue-hover;
            margin-bottom: -1px;
        }
    }
}

// Focus without extra outlining
li.n-list-group-item:focus {
    outline-style: none;
    box-shadow: none;
    border: 1px solid @gray-medium !important;
}

ul.n-list-group {

    > li:first-child.selected {
        border-top: 1px solid transparent;
    }

    > li:last-child.selected {
        border-bottom: 1px solid transparent;
    }

}

li.n-list-group-item.selected {
    font-weight: bold;
    text-shadow: none;
    background-color: @list-selected-invert;
    color: white;
    outline: none;
    border-color: transparent !important;

    &:hover {
        cursor: pointer;
        font-weight: bold;
        text-shadow: none;
        border: 1px solid @gray-medium !important;
        span {
            border-bottom: 1px solid transparent;
        }
    }
}

// Heading for the list, using the aria texts
ul.list-group, select.n-listbox {
    &:before {
        content: attr(aria-label);
        font-weight: normal;
        padding-left: 10px;
        padding-bottom: 1px;
        display: block;
        color: @gray-global;
    }
}

