@import "variables.less";
@import "glyphicons.less";
@import "../w-less/mixins/vendor-prefixes.less";

.n-inputfield {
	background: @input-bg;
	font-weight: normal;
	height: @input-height-base;
	padding: @padding-base-vertical 6px @padding-base-vertical 11px;
	border: 1px solid @input-border;
	color: @text-color;
	border-radius: 7pt;
	//SPEC 4px margin
	//margin: 4px;
	.placeholder(@input-color-placeholder);
	.box-shadow(~"0 1px 0 #ffffff, inset 1px 1px 0 #dddddd");
	.transition(none); //prevents black content in printing together with form-control class
	-webkit-tap-highlight-color: @blue-selection;

	&:hover {
		border: 1px solid @input-border-hover;
	  	box-shadow: 0 1px 0 #ffffff, inset 0 0 0 1px lighten(@input-border-hover, 30%);
	}

	&::-moz-selection {
		color: @text-input-color-selected-font;
		background: @blue-selection;
		border: 1px solid @input-border;
	}
	&::selection {
		color: @text-input-color-selected-font;
		background: @blue-selection;
		border: 1px solid @input-border;
	}
	&:focus {
		border: 1px solid @input-border-focus;
		outline: none;
		.box-shadow(~"0 1px 0 #ffffff, inset 1px 1px 0 #ffffff");
	}

	&:disabled, &[disabled] {
		background: @input-bg-disabled;
		color: @input-color-disabled;
		font-weight: normal; //for error feedback font change
		border: 1px solid @input-border-disabled;
		.box-shadow(none);
		.placeholder(@input-color-disabled);
	}

	&:read-only {
		color: @text-color;
		border: transparent;
		background: transparent;
		.box-shadow(none);
	}

	&[readonly] {
		color: @text-color;
		border: transparent;
		background: transparent;
		.box-shadow(none);
	}

	&:-moz-read-only {
		color: @text-color;
		border: transparent;
		background: transparent;
		.box-shadow(none);
	}

	&-small {
		height: @input-height-small;
		padding-top: @padding-small-vertical;
		padding-bottom: @padding-small-vertical;
		padding-right: 6px;
	}

	&-uneditable:-moz-read-only {
		color: @text-color;
		background: @input-bg-disabled;
		border: 1px solid @input-border-disabled;
		.box-shadow(none);
	}

	&-uneditable:read-only {
		background: @input-bg-disabled;
		color: @text-color;
		border: 1px solid @input-border-disabled;
		.box-shadow(none);
	}

	&-uneditable[readonly] {
		background: @input-bg-disabled;
		color: @text-color;
		border: 1px solid @input-border-disabled;
		.box-shadow(none);
	}
}

// Feedback states
.has-error {
	color: @brand-danger;
	font-weight: bold;
	padding-right: 6px;
}

.has-feedback {
	// Enable absolute positioning
	position: relative;
}

.form-control-feedback {
	position: absolute;
	top: 0;
	right: 0;
	z-index: 2; // Ensure icon is above input groups
	display: block;
	height: @input-height-base;
	line-height: @input-height-base;
	text-align: left;
	pointer-events: none;

	> .icon-input-error, .icon-input-error-disabled {
		font-size: @input-height-base;
	}
	&-small {
		height: @input-height-small;
		line-height: @input-height-small;
		> .icon-input-error, .icon-input-error-disabled {
			font-size: @input-height-small;
		}
	}
	> .icon-spinner {
		// Need to raise the icon a bit higher, any better way to do it?
		font-size: @input-height-small - 1px;
	}
}

// Ensure icons don't overlap text
.entry-changed, .valid {
	.n-inputfield, .n-inputfield-small {
		padding-right: 25px;
	}
	.form-control-feedback, .form-control-feedback-small {
		width: 22px;
		//SPEC 4px margin
		//margin-top: 4px;
		margin-right: 4px;
		padding-left: 4px;
	}
}

.input-required {
	.n-inputfield, .n-inputfield-small {
		padding-right: 6px;
	}
	.form-control-feedback, .form-control-feedback-small {
		//SPEC 4px margin
		//margin-top: 4px;
		margin-right: 1px;
		padding-left: 4px;
		width: 24px;
	}
}

.input-error {
	.n-inputfield {
		padding-right: 34px;
	}
	.n-inputfield-small {
		padding-right: 32px;
	}
	.form-control-feedback {
		width: 32px;
		//SPEC 4px margin
		//margin-top: 4px;
		padding-left: 4px;
	}
	.form-control-feedback-small {
		width: 28px;
	}
}
.no-feedback {
	.n-inputfield, 	.n-inputfield-small {
		padding-right: 6px;
	}
	.form-control-feedback, .form-control-feedback-small {
		width: 0;
	}
}

.input-working {
	.n-inputfield {
		padding-right: 27px;
	}
	.form-control-feedback {
		//SPEC 4px margin
		//margin-top: 4px;
		margin-right: 1px;
		padding-top: 3px;
		padding-left: 4px;
		width: 40px;
	}
}