@import "../bootstrap/less/tables.less";
@import "inputfield.less";

@n-table-cell-height: 26px;



caption {
  padding-top: @table-cell-padding;
  padding-bottom: @table-cell-padding;
  color: @text-muted;
  text-align: left;
}
th {
  text-align: left;
}

// Table 
//
// Standard table

.n-table{
  width: 100%;
  max-width: 100%;
  margin-bottom: @line-height-computed;

  > thead > tr > th {
    #gradient > .vertical(@n-table-th-top, @n-table-th-bottom);
  }

  // Cells
  > thead,
  > tbody,
  > tfoot {
    > tr {
      color: @gray-dark;
      font-weight: normal;
      > th,
      > td {
        padding-left: @table-cell-padding;
        padding-right: @table-cell-padding;
        line-height: @n-table-cell-height;
        border-top: 0;
        &.n-cell-dropdown
        {
          width: 200px;
        }
        &.n-cell-control
        {
          padding: 0 1px;
        }
        > .n-inputfield
        {
          width: 100%;
          margin: 0;
          height: @n-table-cell-height;
          padding: 0 @table-cell-padding 0 @table-cell-padding;
          border-radius: 5px;
          box-shadow: none;
          border: 1px solid @n-table-border-color;

          &:focus {
            border: 1px solid @n-table-border-color;
            box-shadow: none;
          }
        }

        .checkbox-small{
          line-height: 14px;
          margin: 0;
          input[type='checkbox']{
            position: absolute;
          }
          > label{
            min-height: 14px;
            font-size: 14px;
            padding-left: 14px;
          }
        }
        .btn-group {
          width: 100%;
          margin:0 0 1px 0;
          .dropdown-menu {
            width: 100%;
          }
          .dropdown-toggle {
            width: 100%;
            height:@n-table-cell-height;
            padding-right: 8px;
            padding-left: 8px;
            box-shadow: 0 1px 3px 0 rgba(77, 77, 77, 0);
            border-color: @dropdown-select-caret-border-color;
            &:hover{
              border-color: @blue-hover;
              .selected-caret{
                border-left: 1px solid @dropdown-select-caret-border-color;
              }
            }
            &:focus:not(:hover):not([aria-expanded=true])
            {
              color: @dropdown-text;
              text-shadow: none ;
              background-image: linear-gradient(to bottom, @button-basic-gradient-start 0%, @button-basic-gradient-end 100%) ;
            }
            .selected-label {
              float: left;
              border-right: none;
            }
            .selected-caret {
              float: right;
              .caret {
                border-top:4px solid @gray-global;
                float: right;
                margin-top: 10px;
                margin-left: 8px;
                margin-bottom: 10px;
              }
            }
          }

        }
      }
      }
    }
  }

  // Bottom align for column headings, styles in header
  > thead > tr > th {
    color: @gray-global;
    font-weight: normal;
    #gradient > .vertical(@n-table-th-top, @n-table-th-bottom);
    vertical-align: bottom;
    border-bottom: 2px solid @table-border-color;
    line-height: @n-table-cell-height;
  }
  // Remove top border from thead by default
  > caption + thead,
  > colgroup + thead,
  > thead:first-child {
    > tr:first-child {
      > th,
      > td {
        border-top: 0;
      }
    }
  }
  // Account for multiple tbody instances
  > tbody + tbody {
    border-top: 2px solid @table-border-color;
  }
  // Nesting
  .table {
    background-color: @body-bg;
  }


  // Grid lines
  //
  // White background with border lines between all cells.

  .n-sortable
  {
    > span.icon-arrow,
    > span.icon-arrow-up {
      position: relative;
      top:2px;
      left:5px;
    }

    > span {
      > span.icon-arrow {
        position: relative;
        top:6px;
        margin-left: -17.5px;
        left:5px
      }

      > span.icon-arrow-up {
        position: relative;
        top:-2px;
        left:5px;
      }
    }
    cursor: pointer;
  }

  .n-table-grid-lines{
    > thead,
    > tbody,
    > tfoot {
      > tr {
        > th,
        > td {
          border: 1px solid @n-table-border-color;
          &:first-child {
            border-left: none;
          }
          &:last-child {
            border-right: none;
          }
        }
        &:last-child {
          > th,
          > td {
            border: 1px solid @n-table-border-color;
            border-bottom: none;
            &:first-child {
              border-left: none;
            }
            &:last-child {
              border-right: none;
            }
          }
        }
      }
    }
    > thead > tr {
      > th {
        border: 1px solid @n-table-border-color;
        border-top: none;
      }
      > td {
        border-bottom-width: 2px;
      }
    }
    > tbody > tr:nth-child(odd) {
      background-color: @n-table-row-white;
    }
    > tbody > tr:nth-child(even) {
      background-color: @n-table-row-white;
      > td > .n-inputfield  {
        width: 100%;
        margin: 0px;
        height: @n-table-cell-height;
        padding: 0px @table-cell-padding 0px @table-cell-padding;
        border-radius: 5px;
        box-shadow: none;
        border: 1px solid @n-table-border-color;

        &:focus {
          border: 1px solid @n-table-border-color;
          box-shadow: none;
        }
      }
    }
  }

  // Zebra-striping
  //
  // Alternating white and grey background with borders between all columns.

  .n-table-striped {
    > thead,
    > tbody,
    > tfoot {
      > tr {
        > th,
        > td {
          border-left: 1px solid @n-table-border-color;
          border-right: 1px solid @n-table-border-color;
          &:first-child {
            border-left: none;
          }
          &:last-child {
            border-right: none;
          }
        }
      }
    }
    > thead > tr {
      > th {
        border-top: 0px;
        border-bottom: 1px solid @table-border-color;
        border-left: 1px solid @n-table-border-color;
        border-right: 1px solid @n-table-border-color;
      }
      > td {
        border-bottom-width: 2px;
      }
    }
    > tbody > tr:nth-child(odd) {
      background-color: @n-table-row-white;
    }
    > tbody > tr:nth-child(even) {
      background-color: @n-table-row-grey;
    }
  }

  // Row hover effect for alternating table and grid-line table

  .n-table-hover {
    // Row hover for alternating table
    > tbody > tr:nth-child(odd):hover {
      background-color: @n-table-row-white-hover;
    }
    > tbody > tr:nth-child(even):hover {
      background-color: @n-table-row-grey-hover;
    }
    // Row hover for grid line table
    & .n-table-grid-line {
      > tbody > tr > td:hover {
        background-color: @n-table-row-white-hover;
      }
      > tbody > tr:nth-child(even) > .n-cell-selected:hover,
      > tbody > tr:nth-child(odd) > .n-cell-selected:hover {
        color: @n-table-row-white;
        background-color: @n-table-row-selected;
      }
    }
  }


  // Cell hover effect for alternating table and grid-line table

  .n-table-cell-hover {
    // Cell hover for alternating table
    > tbody > tr:nth-child(odd) > td:hover {
      background-color: @n-table-row-white-hover;
    }
    > tbody > tr:nth-child(even) > td:hover {
      background-color: @n-table-row-grey-hover;
    }
    > tbody > tr:nth-child(even) > .n-cell-selected:hover,
    > tbody > tr:nth-child(odd) > .n-cell-selected:hover {
      color: @n-table-row-white;
      background-color: @n-table-row-selected;
    }
    // Cell hover for grid line table
    & .n-table-grid-line {
      > tbody > tr > td:hover {
        background-color: @n-table-row-white-hover;
      }
      > tbody > tr:nth-child(even) > .n-cell-selected:hover,
      > tbody > tr:nth-child(odd) > .n-cell-selected:hover {
        color: @n-table-row-white;
        background-color: @n-table-row-selected;
      }
    }
  }

  // Standard table container
  //
  // Standard table with rounded corners and shadow

  .n-table-standard {
    border-radius: 7px;
    box-shadow: 0px 1px 3px 0px #888;
    border-collapse: inherit;
    > tbody
    > tr:last-child {
      > td {
        &:first-child {
          border-radius: 0px 0px 0px 7px;
        }
        &:last-child {
          border-radius: 0px 0px 7px 0px;
        }
      }
    }
    > thead > tr {
      > th {
        &:first-child {
          border-radius: 7px 0px 0px 0px;
        }
        &:last-child {
          border-radius: 0px 7px 0px 0px;
        }
      }
    }
  }

  // Cell error
  //
  // Text has bold error color

  .n-cell-error {
    color: @brand-danger;
    font-weight: bold;
  }

  // Cell or row selected
  //
  // Text and background has selected color

  .n-cell-selected {
    color: @n-table-row-white;
    background-color: @n-table-row-selected;

    .checkbox-small label{
      color: @n-table-row-white;
    }
  }
