@import "../bootstrap/less/forms.less";
@import "variables.less";

label {
    font-weight: normal;
    color: @gray-global;
    margin-left: 0;
}

.form-horizontal {
    @media (min-width: @screen-sm-min) {
        .control-label {
            text-align: left;
            padding-top: 6px;
            padding-bottom: 6px;
        }
    }
}

// Special labels
.control-label-disabled {
    font-weight: normal;
    color: @input-color-disabled;
}

.radio input[type="radio"], .radio-inline input[type="radio"], .checkbox input[type="checkbox"], .checkbox-inline input[type="checkbox"] {
    margin-left: 0;
}

input[type=checkbox] {
    -moz-opacity: 0;
    opacity: 0;
    margin: 0;
    //no opacity for IE to get normal checkbox visible
    //filter: alpha(opacity=0);
}

.radio label, .checkbox label {
    font-weight: 400;
    min-height: 22px;
    margin-bottom: 0px;
    padding-left: 28px;
    cursor: pointer;
    color: @gray-dark;
}

.checkbox {
    margin-top: 0px;
    label {

        //FIXME: The :not selector is a hack for getting IE8 to ignore
        //	     the definition of the ::before pseudo-element.
        &:not(div):before {
            content: "";
            display: inline-block;

            width: 20px;
            height: 20px;

            position: absolute;
            left: 0;

            background: #fff;
            color: @input-color;
            border: 1px solid @input-border;
            box-shadow: 0px 1px 3px 0px @input-border;
            border-radius: 4px;
        }
    }

    input[type=checkbox] {
        &:checked + label:before {
            font-family: 'Icons';
            content: "\e600";
            font-size: 14px;
            text-align: center;
            line-height: 16px;
        }

        &:hover + label:before {
            border: 1px solid @input-border-hover;
            box-shadow: 0px 1px 3px 0px @input-border, 0px 0px 0px 1px lighten(@input-border-hover, 30%) inset;
        }

        &:active + label:before {
            border: 1px solid @input-border-focus;
            box-shadow: 0px 1px 3px 0px @input-border-focus inset;
        }

        &:focus + label:before {
            border: 1px solid @input-border-focus;
            box-shadow: 0px 1px 3px 0px @input-border-focus;
        }

        &:disabled + label {
            color: @input-color-disabled;
            cursor: default;
        }
        &[disabled] + label {
            color: @input-color-disabled;
            cursor: default;
        }

        &:disabled + label:before {
            border: 1px solid @input-border-disabled;
            background: @input-bg-disabled;
            box-shadow: 0px 1px 3px 0px @input-border-disabled;
            color: @input-color-disabled;
            cursor: default;
        }

        // For tri-state checkbox
        &:indeterminate + label:before {
            font-family: 'Icons';
            content: "\25AC";
            font-size: 8px; //14px;
            text-align: center;
            line-height: 16px;
            width: 16px;
            transform: scale(1.3,1);
            -webkit-transform: scale(1.3,1);
            -moz-transform: scale(1.3,1);
            -ms-transform: scale(1.3,1);
            -o-transform: scale(1.3,1);
            position:absolute;
            left:2px;
        }
    }

    &-small {
        margin-bottom: 2px;
        margin-top: -2px;
        label {
            font-size: 0.786em;
            font-weight: 400;
            margin-bottom: 0;
            margin-left: 6px;
            color: @gray-dark;

            //FIXME: The :not selector is a hack for getting IE8 to ignore
            //	     the definition of the ::before pseudo-element.
            &:not(div):before {
                content: "";
                display: inline-block;

                width: 14px;
                height: 14px;

                position: absolute;
                left: 0;

                background: #fff;
                color: @input-color;
                border: 1px solid @input-border;
                box-shadow: 0px 1px 3px 0px @input-border;
                border-radius: 3px;
            }
        }

        input[type=checkbox] {
            &:checked + label:before {
                font-family: 'Icons';
                content: "\e600";
                font-size: 10px;
                text-align: center;
                line-height: 12px;
            }
            &:hover + label:before {
                border: 1px solid @input-border-hover;
                box-shadow: 0px 1px 3px 0px @input-border, 0px 0px 0px 1px lighten(@input-border-hover, 30%) inset;
            }

            &:active + label:before {
                border: 1px solid @input-border-focus;
                box-shadow: 0px 1px 3px 0px @input-border-focus inset;
            }

            &:focus + label:before {
                border: 1px solid @input-border-focus;
                box-shadow: 0px 1px 3px 0px @input-border-focus;
            }

            &:disabled + label {
                color: @input-color-disabled;
                cursor: default;
            }
            &[disabled] + label {
                color: @input-color-disabled;
                cursor: default;
            }

            &:disabled + label:before {
                border: 1px solid @input-border-disabled;
                background: @input-bg-disabled;
                box-shadow: 0px 1px 3px 0px @input-border-disabled;
                color: @input-color-disabled;
                cursor: default;

            }
        }
    }
}

.n-file-upload {

    input[type=file] {
        overflow: hidden;
        position: absolute;
        left: -1000px;
        opacity: 0;
        -moz-opacity: 0;
    }

}

.n-form-control-static {
    padding-top: @padding-base-vertical;
    padding-bottom: @padding-base-vertical;
    padding-left: 0.7em;
    padding-right: 0.5em;
    margin: 4px;
    //margin-bottom: 0;
    border: transparent;
    background: transparent;
    .box-shadow(none);
    color: @text-color;
    outline: none;

    &-small {
        padding-top: @padding-small-vertical;
        padding-bottom: @padding-small-vertical;
    }

    &::selection {
        color: @text-input-color-selected-font;
        background: @blue-selection;
    }

}

// Radiobutton

input[type=radio] {
    -moz-opacity: 0;
    opacity: 0;
    margin: 0;
    //no opacity for IE to get normal checkbox visible
    //filter: alpha(opacity=0);
}

.radio {
    margin-top: 0px;
    label {
        position: relative;
        //FIXME: The :not selector is a hack for getting IE8 to ignore
        //	     the definition of the ::before pseudo-element.
        &:not(div):before {
            content: "";
            display: inline-block;

            width: 20px;
            height: 20px;

            position: absolute;
            left: 0;

            background: #fff;
            color: @input-color;
            border: 1px solid @input-border;
            box-shadow: 0px 1px 3px 0px @input-border;

            border-radius: 10px;
        }
    }
    input[type=radio] {
        &:checked + label:after {
            content: "";
            display: inline-block;

            width: 8px;
            height: 8px;

            position: absolute;
            left: 6px;
            top: 6px;

            background: #666666;
            border-radius: 50%;
        }

        &:hover + label:before {
            border: 1px solid @input-border-hover;
            box-shadow: 0px 1px 3px 0px @input-border, 0px 0px 0px 1px lighten(@input-border-hover, 30%) inset;
        }

        &:active + label:before {
            border: 1px solid @input-border-focus;
            box-shadow: 0px 1px 3px 0px @input-border-focus inset;
        }

        &:focus + label:before {
            border: 1px solid @input-border-focus;
            box-shadow: 0px 1px 3px 0px @input-border-focus;
        }

        // To remove focus border
        &:focus {
            outline: initial;
        }

        &:disabled + label {
            color: @input-color-disabled;
            cursor: default;
        }
        &[disabled] + label {
            color: @input-color-disabled;
            cursor: default;
        }

        &:disabled + label:before {
            border: 1px solid @input-border-disabled;
            background: @input-bg-disabled;
            box-shadow: 0px 1px 3px 0px @input-border-disabled;
            color: @input-color-disabled;
            cursor: default;

        }
    }
}

.radio-small:extend(.radio) {
    margin-top: -2px; //0px; //2px;
    margin-bottom: -2px; //0px; //2px;
    label {
        font-size: 0.786em;
        font-weight: 400;
        margin-bottom: 0;
        margin-left: -5.5px;
        position:static;
        min-height: 20px;

        color: @gray-dark;

        //FIXME: The :not selector is a hack for getting IE8 to ignore
        //	     the definition of the ::before pseudo-element.
        &:not(div):before {
            width: 14px;
            height: 14px;

            bottom: 5px;
        }
    }

    input[type=radio] {
        &:checked + label:after {
            width: 6px;
            height: 6px;

            left: 4px;
            top: 8px;
        }

    }
}

