.n-banner-links-collapse {

  height: 40px;

  &-dropdown-toggle {
    padding: 10px;
    position: absolute;
    right: 0;
    #gradient.vertical(#D8D9DA, #ADAEAE) !important;

    &:before {
      content: "";
      border-left: 1px solid;
      position: absolute;
      top: 0;
      left: 0;
      height: 40px;
      display: block;
      box-shadow: 0 0 8px 0 #000;
      z-index: -1;
    }

    &[aria-expanded=true]{
      border-radius: 0;
    }

    &:focus:not(.disabled):not([aria-expanded=true]),
    &:hover:not(.disabled)[aria-expanded=false]{
      padding: 8px;
      border: 2px solid #00a1cc;
      background: #fff !important;
      &:before{
        display: none;
      }
    }
    
  }

  &-dropdown-menu,
  .n-banner-dropdown-links-collapse-dropdown-menu{
    right: 0;
    left: auto;
    li a:hover span{
      //border: none;
    }

    .n-dropdown-sub-menu{

    }
  }

}